.client-container {
  display: flex;
}

.chat-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  width: 100%;
  height: 98vh;
  border-bottom: none;
  border: 5px 3px solid black;
}

.no-user {
  margin-top: 2.5rem;
  height: 8rem;
  width: 25rem;
  background-color: purple;
  padding: 1rem;
  text-align: center;
  border-radius: 16px;
  box-shadow: 10px 0 5px -3px rgba(129, 129, 129, 0.616);
}

.no-user_container {
  display: flex;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background-color: lavenderblush;
}

.no-user_title {
  color: white;
}
